import React from 'react';
import { BrowserRouter } from 'react-router-dom';
import { createStore } from 'redux';
import { Provider } from 'react-redux';
import App from './App';
import { getBaseName } from '@redhat-cloud-services/frontend-components-utilities/helpers';
import './i18n';
import * as serviceWorker from './serviceWorker';

const store = createStore((state) => state, ['Use Redux']);

const AppEntry = () => (
  <Provider store={store}>
    <BrowserRouter
      basename={getBaseName(
        window.location.pathname,
        1 // level required for correct path parsing
      )}
    >
      <App />
    </BrowserRouter>
  </Provider>
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();

const origin = window.location.origin;

const env =
  origin.indexOf('dev') != -1 || origin.indexOf('localhost') != -1
    ? ['dev.', '-dev']
    : origin.indexOf('qa') != -1
    ? ['qa.', '-qa']
    : origin.indexOf('stage') != -1
    ? ['stage.', '-stage']
    : ['', ''];

window['runConfig'] = {
  apiUrl: 'https://access.' + env[0] + 'redhat.com/subscriptions/link/api/',
  hrefConfig: 'https://access.' + env[0] + 'redhat.com/',
  hrefEnv: env[0],
};

export default AppEntry;
